@import '../../style/base.scss';

.homeContainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: 100vh;
  padding: 0;

}

.homeColumn {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  justify-content: center;
}

.leftColumn,
.rightColumn {
  background-color: #fff;
  position: sticky;
  /*  sticky */
  top: 0;
  /*  */
  height: 100vh;
  /*  viewport  */
}

.leftColumn {
  margin-right: 24px;
  flex: 0 0 200px;
  z-index: 1;
}

.rightColumn {
  min-width: calc(70% - 200px);
}

.middleColumn {
  min-width: 30%;
  position: relative;
  background-color: #fff;

  overflow-y: auto;

  // ()
  scrollbar-width: none;
  -ms-overflow-style: none; //ms

  &::-webkit-scrollbar {
    //scss
    display: none;
  }

}